.List{
    text-transform:capitalize;
    list-style-type: none;
    font-size: 20px;
    margin: 10px;
}
.enter{
    opacity: 0;
    transform: translateX(-100%);
}
.enterActive{
    opacity: 1;
    transform: translateX(0);
    transition:all 250ms linear;
}

.exit{
    opacity: 1;
    transform: translateX(0);
  
}
.exitActive{
    opacity: 0;
    transform: translateX(-100%);
    transition: all 250ms linear;
}
 
